import { request } from 'utils';

const prefix = '/api/students';
const prefixCategory = '/api/categories';

export const getQuickLinkTrainTypeNo = ({ query } = {}) =>
  request({
    prefix: prefixCategory,
    url: '/QuickLink/GetTrainTypeNo',
    method: 'GET',
    params: { query },
    data: {},
  });

