import React, { Component } from 'react';
import routeConstant from 'routes/route/routeConstant';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import { InfoCard } from 'components/common';
import {
  getQuickLinkBonusGetBonusTypeChildNo,
  getQuickLinkBonusGetBonusTypeNo,
  getQuickLinkBonusGetBonusNoteNo,
  getBonusEstimationNo,
  getClassBonusEstimationNo,
  getClassBonusNoteNo,
} from 'services/quicklink';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Cấp khen thưởng',
    service: getQuickLinkBonusGetBonusTypeNo,
    url: routeConstant.KHUYEN_THUONG__CAP_KHEN_THUONG,
  },
  {
    title: 'Loại khen thưởng',
    service: getQuickLinkBonusGetBonusTypeChildNo,
    url: routeConstant.KHUYEN_THUONG__LOAI_KHEN_THUONG,
  },
  {
    title: 'QĐ khen thưởng',
    service: getQuickLinkBonusGetBonusNoteNo,
    url: routeConstant.KHUYEN_THUONG__NHAP_QDKT_CA_NHAN,
  },
  {
    title: 'QĐ khen thưởng lớp',
    service: getClassBonusNoteNo,
    url: routeConstant.KHUYEN_THUONG__NHAP_QDKT_LOP,
  },
  {
    title: 'SV đang xét',
    service: getBonusEstimationNo,
    url: routeConstant.KHUYEN_THUONG__XET_CA_NHAN,
  },
  {
    title: 'Lớp đang xét',
    service: getClassBonusEstimationNo,
    url: routeConstant.KHUYEN_THUONG__XET_TAP_THE,
  },
];
class DashboardBonu extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardBonu {...props} />
  </Page>
);



